@import '../../assets/scss/mixins';

.header-video {
  width: 100%;
  overflow: hidden;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.header-container {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  z-index: 500;
}

.header-overlay {
  background: linear-gradient(to top, #181818, transparent 50%);
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
}

.header-description {
  position: absolute;
  bottom: 8%;
  left: 0.4rem;
  @include breakpoint(sm) {
    bottom: 5%;
    left: 3em;
  }
}

.header-title {
  font-size: 2rem;
  width: 100%;
  @include breakpoint(lg) {
    font-size: 3rem;
    padding-bottom: 1rem;
  }
}

.header-button-container {
  display: flex;
  align-items: center;
}

.header-play-button {
  align-items: center;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  user-select: none;
  margin-right: 0.5rem;
  padding-right: 1.5rem;
  height: 2rem;
  &:hover {
    background-color: rgb(214, 214, 214);
  }
  @include breakpoint(md) {
    height: 2.4rem;
  }
}

.header-play-button-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.5rem;
  width: 1.5rem;
  margin-left: 1rem;
  @include breakpoint(md) {
    height: 1.8rem;
    width: 1.8rem;
  }
  @include breakpoint(lg) {
    height: 2rem;
    width: 2rem;
  }
}

.modal-volume-button-wrapper {
  position: absolute;
  bottom: 6%;
  left: 82%;
  @include breakpoint(sm) {
    left: 90%;
  }
  @include breakpoint(md) {
    left: 89%;
  }
  @include breakpoint(lg) {
    left: 90%;
  }
  @include breakpoint(xlg) {
    left: 94%;
  }
}
