@import '../../assets/scss/mixins';

.modal-footer-container {
  background-color: #181818;
  padding-bottom: 2em;
}

.modal-footer-header {
  font-weight: 400;
  padding-top: 48px;
  margin-bottom: 20px;
}

.modal-footer-title {
  font-size: 1.6rem;
  @include breakpoint(md) {
    font-size: 2rem;
  }
  span {
    font-weight: 100;
  }
}

.modal-footer-item {
  font-size: 14px;
  line-height: 20px;
  margin: 0.5em;
  margin-left: 0;
  word-break: break-word;
}
